.App {
  text-align: center;
}
.weather-inner{
 display: flex;
 justify-content: center;
}
.weather-box {
  height: 300px;
  width: 500px;
  border-radius: 10px;
  background: url('./images/weather.png');
  color: white;
  background-position: center;
  background-size: cover;
}

.palce{
  display: block;
    height: 40px; 
    text-align: center;
    padding-top: 15px;
    font-size: 50px;
    text-transform: uppercase;
}
.weather-info{
  display: flex;
  justify-content:space-around;
  align-items: center;
}
.act-wether{
  display: flex;
  flex-direction: column;
}
.other-details{
  display: flex;
    flex-direction: column;
    align-items: flex-start;
}
.temp{

  font-size: 55px;
}


